.projects-page {
  padding: 2rem 0;
  min-height: 100vh;
  background: #f8f9fa;
}

.projects-header {
  text-align: center;
  margin-bottom: 3rem;
}

.projects-header h1 {
  font-size: 3rem;
  color: #333;
  margin-bottom: 1rem;
}

.projects-header p {
  font-size: 1.1rem;
  color: #666;
  max-width: 600px;
  margin: 0 auto;
}

/* Filters */
.filters {
  display: flex;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
  justify-content: center;
}

.search-box {
  flex: 1;
  min-width: 250px;
  max-width: 400px;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 25px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  background: white;
}

.search-input:focus {
  outline: none;
  border-color: #667eea;
}

.status-filter {
  min-width: 150px;
}

.status-select {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 25px;
  font-size: 1rem;
  background: white;
  cursor: pointer;
  transition: border-color 0.3s ease;
}

.status-select:focus {
  outline: none;
  border-color: #667eea;
}

/* Projects Grid */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.project-card {
  background: white;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-image {
  position: relative;
  height: 250px;
  overflow: hidden;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-status {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  color: white;
  font-size: 0.8rem;
  font-weight: 600;
  text-transform: capitalize;
}

.project-content {
  padding: 2rem;
}

.project-content h3 {
  margin-bottom: 0.5rem;
  color: #333;
  font-size: 1.5rem;
}

.project-date {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.project-description {
  color: #666;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.project-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background: #f0f0f0;
  color: #666;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.project-links {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.project-link {
  color: #667eea;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  background: #f8f9fa;
  transition: all 0.3s ease;
}

.project-link:hover {
  color: #764ba2;
  background: #e9ecef;
}

/* No Projects Message */
.no-projects {
  text-align: center;
  padding: 3rem;
  background: white;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.no-projects h3 {
  color: #333;
  margin-bottom: 1rem;
}

.no-projects p {
  color: #666;
}

/* Responsive Design */
@media (max-width: 768px) {
  .projects-header h1 {
    font-size: 2rem;
  }
  
  .filters {
    flex-direction: column;
    align-items: center;
  }
  
  .search-box {
    width: 100%;
    max-width: none;
  }
  
  .status-filter {
    width: 100%;
    max-width: 200px;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
  }
  
  .project-content {
    padding: 1.5rem;
  }
  
  .project-links {
    flex-direction: column;
  }
  
  .project-link {
    text-align: center;
  }
} 